.IconWrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.Icon {
  margin-left: 40px;
  margin-right: 40px;
  margin-bottom: 30px;
  margin-top: 0px;
  height: 40px;
  transition: 0.2s;
}

.Icon:hover {
  margin-top: -10px;
  height: 45px;
  opacity: 0.5;
}
