@import url(https://fonts.googleapis.com/css?family=Crete+Round&display=swap);
.App {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background-color: #4a4848;
  font-family: "Crete Round", serif;
  color: white;
  overflow: scroll; }

a {
  text-decoration: none;
  color: white;
  padding-bottom: 1px; }

a:hover {
  border-bottom: 2px solid white; }

.PageWrapper {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column; }

.MainPageContentWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.MeImage {
  margin: 40px; }

.TitleText {
  margin: 5px;
  font-size: 3rem; }

.SubtitleText {
  margin: 5px;
  margin-bottom: 50px;
  text-align: center; }

.IconWrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center; }

.Icon {
  margin-left: 40px;
  margin-right: 40px;
  margin-bottom: 30px;
  margin-top: 0px;
  height: 40px;
  -webkit-transition: 0.2s;
  transition: 0.2s; }

.Icon:hover {
  margin-top: -10px;
  height: 45px;
  opacity: 0.5; }

.NavBar {
  display: flex;
  flex-direction: row;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  min-height: 100px; }

.NavbarLink {
  font-size: 24px;
  margin-left: 80px;
  margin-right: 80px; }

.AboutContentWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 50px; }

.AboutTextContent {
  text-align: center; }

.CategoryTitle {
  font-style: italic;
  text-decoration: underline; }

.AboutPageSwitch {
  display: flex;
  flex-direction: row;
  font-size: 1.2rem;
  margin-top: 50px; }

.ActiveSwitch {
  border-top: solid white 5px;
  border-bottom: solid white 5px;
  padding-left: 20px;
  padding-right: 20px; }

.InactiveSwitch {
  border-top: solid #8d8c8c 5px;
  border-bottom: solid #8d8c8c 5px;
  padding-left: 20px;
  padding-right: 20px; }

.InactiveSwitch:hover {
  border-top: solid white 5px;
  border-bottom: solid white 5px; }

.CardWrapper {
  position: relative;
  border-radius: 10px;
  height: 225px;
  width: 200px;
  box-shadow: 5px 4px 50px black;
  margin: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
  -webkit-transition: 0.3s;
  transition: 0.3s; }

.CardWrapper:hover {
  height: 235px;
  width: 210px; }

.CardTitle {
  position: absolute;
  top: 75%;
  padding: 5px;
  padding-left: 20px;
  padding-right: 20px;
  background-color: #4a4948;
  border-radius: 5px; }

.ProjectsContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center; }

